import { createRouter, createWebHistory } from 'vue-router'
// import firebase from "firebase";
import HomeViews from './home/index';
import AppContaine from "../components/AppContainer.vue";
import AboutViews from './about/index';
// import About from "../views/About.vue";
import TarefaViews from "./tarefas/index";
import UsuarioViews from "./usuarios/index";
import Login from "../views/authentication/Login.vue";
import Signup from "../views/authentication/Signup.vue";
import LoginViews from "./login/index";
import ConjuntosViews from "./conjunto/index"

const routes = [{
        path: '/app',
        name: 'Main',
        component: AppContaine,
        children: [
            HomeViews,
            AboutViews,
            TarefaViews,
            UsuarioViews,
            LoginViews,
            ConjuntosViews
        ]
    },
    {
        path: '/login',
        name: 'Login',
        component: Login,


    },
    {
        path: '/signup',
        name: 'signup',
        component: Signup,


    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})


