import { RouterView } from "vue-router";
const Conjuntos = () =>
    import ("@/views/conjunto/Clientes.vue");
const NovoConjuntos = () =>
    import ("@/views/conjunto/NovoCliente.vue");
const TarefasCliente = () =>
    import ("@/views/conjunto/ConjuntoDeCliente/ListaTarefasCliente.vue");
const NovoTarefasCliente = () =>
    import ("@/views/conjunto/ConjuntoDeCliente/NovoTarefa.vue");

const ConjuntosViews = {
    path: "conjunto",
    name: "Conjunto",
    component: RouterView,
    children: [{
            path: "/conjuntos",
            name: "Conjuntos",
            component: Conjuntos,
            meta: {
                requiresAuth: true
            }
        },

        {
            path: "/conjunto/novo",
            name: "Novo cadastro de conjuntos",
            component: NovoConjuntos,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/editar/:clienteId",
            name: "Editar cadastro de conjuntos",
            component: NovoConjuntos,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/:visualizar/:clienteId",
            name: "Visualizar cadastro de tarefa",
            component: NovoConjuntos,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/tarefas-cliente/:clienteId",
            name: "Taréfas do cliente",
            component: TarefasCliente,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/tarefas-cliente/novo/:clienteId",
            name: "Novo taréfas cliente",
            component: NovoTarefasCliente,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/tarefas-cliente/editar/:clienteId/:tarefaId",
            name: "Editar taréfas cliente",
            component: NovoTarefasCliente,
            meta: {
                requiresAuth: true
            }
        },
        {
            path: "/conjunto/tarefas-cliente/:visualizar/:clienteId/:tarefaId/",
            name: "Visualizar taréfas cliente",
            component: NovoTarefasCliente,
            meta: {
                requiresAuth: true
            }
        },


    ],
};

export default ConjuntosViews;